%icon-font {
  &::before {
    display        : inline-block;
    font-family    : 'iconfont';
    font-style     : normal;
    font-weight    : normal;
    vertical-align : middle;
  }
}

%clearfix {
  &::before, &::after {
    content : " ";
    display : table;
  }
  &::after {
    clear : both;
  }
}